<script>
import Vue from 'vue'
import axios from 'axios';
import VueNotifications from 'vue-notifications'
import miniToastr from 'mini-toastr'// https://github.com/se-panfilov/mini-toastr
import {restapiPrefix,wprestapiPrefix} from './../../variables.js'
import router from '../../../router';
import Loading from 'vue-loading-overlay';
import 'vue-loading-overlay/dist/vue-loading.css';
import moment from 'moment';


const toastTypes = {
  success: 'success',
  error: 'error',
  info: 'info',
  warn: 'warn'
}

const options = {
  success: toast,
  error: toast,
  info: toast,
  warn: toast
}

miniToastr.init({types: toastTypes})

function toast ({title, message, type, timeout, cb}) {
  return miniToastr[type](message, title, timeout, cb)
}

export default {
  name: 'message',
  //props: ['logId','from'],
  props: {
    logId: 0,
    from: '',
  },
  data () {
    return {
      userId:'',
      messageData:[]
    }
  },
  beforeCreate: function () {
    if(localStorage.user_id == undefined || localStorage.user_id == "0")
    {        
      router.push({ name: "Login"})
    } 
  },
  created() {
    this.userId = localStorage.user_id;
    this.getSingleMessageLog();
  },
  mounted() {
      /*if (this.logId) {
        alert(this.logId);
        this.getSingleMessageLog();
      }*/
  },
  methods:{
    getSingleMessageLog(){
      let asksUri = restapiPrefix+'/sendMessageLog/getSingleMessageLog/'+this.userId+'/'+this.logId;
      this.axios.post(asksUri).then(response => {
        this.messageData = response.data[0];
        console.log(this.messageData);
      });
    },
    converHTML(str){
      const doc = new DOMParser().parseFromString(str, "text/html");
      const textContent = doc.documentElement.textContent;
      //console.log(textContent);
      return textContent;
    },
    deletelog()
    {
      var logdelete = [];

      logdelete.push(this.messageData.id);
        console.log('logdelete===>'+logdelete);
        if(logdelete.length > 0)
        {
          var param = {
            agencyId: this.userId,
            messageLog: logdelete,
          }

          let asksUri = restapiPrefix+'/sendMessageLog/moveToTrash';
          this.axios.post(asksUri,param).then(response => {
            if(response.data.status == "error")
            {
              miniToastr.error(response.data.message, 'No Records found');
            }
            else{
              router.push({ name: "Trash"})
              miniToastr.success('Moved to Trash', 'Success');
            }
            
            console.log(this.messageData);
          });
        }
        else{
          miniToastr.info('Please select record to delete', 'Info');
        }
    },
    deletelogForever()
    {
      var logdelete = [];
      
      logdelete.push(this.messageData.id);
      console.log('logdelete===>'+logdelete);

        if(logdelete.length > 0)
        {
          var param = {
            agencyId: this.userId,
            messageLog: logdelete,
          }

          let asksUri = restapiPrefix+'/sendMessageLog/deletelogForever';
          this.axios.post(asksUri,param).then(response => {
            if(response.data.status == "error")
            {
              miniToastr.error(response.data.message, 'No Records found');
            }
            else{
              router.push({ name: "Trash"})
              miniToastr.success('Deleted Successfully', 'Success');
            }
            console.log(this.trashMessages);
          });
      }
      
    },
    restorelogfromTrash()
    {
      var logdelete = [];
      
      logdelete.push(this.messageData.id);
      console.log('logdelete===>'+logdelete);
        if(logdelete.length > 0)
        {
          var param = {
            agencyId: this.userId,
            messageLog: logdelete,
          }

          let asksUri = restapiPrefix+'/sendMessageLog/restorelogfromTrash';
          this.axios.post(asksUri,param).then(response => {
            if(response.data.status == "error")
            {
              miniToastr.error(response.data.message, 'No Records found');
            }
            else{
              router.push({name: 'Message', params: {logId: this.logId}})
              miniToastr.success('Restored Successfully', 'Success');
            }
            
            console.log(this.trashMessages);
          });
      }
    }
  },
  filters: {
    pretty: function(value) {
      //return JSON.stringify(JSON.parse(value));
      if(value !== undefined)
      {
        var a = value.replace(/[\[\]"']+/g,'');
        var b = a.split(',');
        return a;
      }
    },
    formatDate: function(dt){
      //return moment(String(dt)).format('MM/DD/YYYY hh:mm')
      return dt;
    }
  }
}
